<template>
  <div class="evaluate">
      <div v-for="(item,index) in evaluate" :key="index" class="goods">
          <div class="top clearfix">
              <img src="../../assets/test/goods.png" class="fl" alt="">
              <div class="fl">
                  <p class="two_t_h name">芒果酸奶</p>
                  <p class="three_t_m">规格：150g/盒</p>
              </div>
          </div>
          <div class="center">
            <ul>
                <li>
                    <span class="two_t_h">味道：</span>
                    <rater :font-size="24" active-color="#FCCE00" v-model="item.wd"></rater>
                    <span v-show="item.wd==5" class="manyi two_t_m">超级满意</span>
                    <span v-show="item.wd==4" class="manyi two_t_m">满意</span>
                    <span v-show="item.wd==3" class="manyi two_t_m">一般</span>
                    <span v-show="item.wd==2" class="manyi two_t_m">不满意</span>
                    <span v-show="item.wd==1" class="manyi two_t_m">很差</span>
                </li>
                <li>
                    <span class="two_t_h">配送：</span>
                    <rater :font-size="24" active-color="#FCCE00" v-model="item.ps"></rater>
                    <span v-show="item.ps==5" class="manyi two_t_m">超级满意</span>
                    <span v-show="item.ps==4" class="manyi two_t_m">满意</span>
                    <span v-show="item.ps==3" class="manyi two_t_m">一般</span>
                    <span v-show="item.ps==2" class="manyi two_t_m">不满意</span>
                    <span v-show="item.ps==1" class="manyi two_t_m">很差</span>
                </li>
                <li>
                    <span class="two_t_h">服务：</span>
                    <rater :font-size="24" active-color="#FCCE00" v-model="item.fw"></rater>   
                    <span v-show="item.fw==5" class="manyi two_t_m">超级满意</span>
                    <span v-show="item.fw==4" class="manyi two_t_m">满意</span>
                    <span v-show="item.fw==3" class="manyi two_t_m">一般</span>
                    <span v-show="item.fw==2" class="manyi two_t_m">不满意</span>
                    <span v-show="item.fw==1" class="manyi two_t_m">很差</span>                                                                                              
                </li>
            </ul>
          </div>
          <div class="content">
              <textarea maxlength="130" placeholder="感觉怎么样跟我们描述一下吧~" v-model="item.pj"></textarea>
              <p class="clearfix">
                  <span class="fr zishu three_t_l">{{item.pj.length}}/130</span>
              </p>
          </div>
      </div>
      <div class="submit">
        <button class="bluebtn">提交</button>
      </div>
  </div>
</template>

<script>
import { Rater } from "vux";

export default {
  name: "Evaluate",
  data() {
    return {
      data1: 5,
      data2: 5,
      data3: 5,
      evaluate:[
          {
              id:1,
              wd:1,
              ps:5,
              fw:4,
              pj:'还不错啦！'
          },
          {
              id:2,
              wd:5,
              ps:1,
              fw:5,
              pj:''
          },
          {
              id:3,
              wd:5,
              ps:3,
              fw:5,
              pj:''
          },
      ]
    };
  },
  methods: {},
  components: {
    Rater
  }
};
</script>

<style scoped lang="less">
.evaluate {
  font-size: 0.16rem;
  background-color: #f2f2f2;
  padding-bottom: 0.65rem;
  padding-top: 0.01rem;
  .goods {
    padding: 0 0.27rem 0 0.28rem;
    margin-top: 0.1rem;
    background-color: #fff;
    .top {
      img {
        display: block;
        width: 0.8rem;
        height: 0.8rem;
        border-radius: 0.12rem;
        margin: 0.15rem 0.15rem 0.15rem 0rem;
      }
      .name {
        margin: 0.33rem 0 0.05rem 0;
      }
    }
    .center {
      padding-top: 0.21rem;
      padding-bottom: 0.15rem;
      border-top: 0.01rem solid #f2f2f2;
      ul {
        li {
          &:nth-child(2) {
            padding: 0.2rem 0rem;
          }
          .manyi {
            margin-left: 0.15rem;
          }
        }
      }
    }
    .content {
      font-size: 0.16rem;
      textarea {
        resize: none;
        border: none;
        width: 100%;
        height: 1.1rem;
      }
      p {
        border-top: 0.01rem solid #f2f2f2;
        .zishu {
          margin: 0.05rem 0 0.1rem 0rem;
        }
      }
      div {
          padding: 0.15rem 0rem;
        .submit {
          border: 0.01rem solid #7f7f7f;
          border-radius: 0.03rem;
          background-color: #fff;
          width: 0.6rem;
          height: 0.24rem;
        }
      }
    }
  }
  .submit {
    padding: 0.3rem 0.27rem 0 0.28rem;
  }
}
</style>



